const getDirection = (direction) => {
    if (direction == 'N' || (direction >= 355 && direction < 5)) {
        return 'N'
    } else if (direction == 'NE' || (direction >= 5 && direction < 85)) {
        return 'NE'
    } else if (direction == 'E' || (direction >= 85 && direction < 95)) {
        return 'E'
    } else if (direction == 'SE' || (direction >= 95 && direction < 175)) {
        return 'SE'
    } else if (direction == 'S' || (direction >= 175 && direction < 185)) {
        return 'S'
    } else if (direction == 'SW' || (direction >= 185 && direction < 265)) {
        return 'SW'
    } else if (direction == 'W' || (direction >= 265 && direction < 275)) {
        return 'W'
    } else if (direction == 'NW' || (direction >= 275 && direction < 355)) {
        return 'NW'
    }
    return ''
}
export default getDirection;