import React, { useState, useEffect } from 'react';
import useOceanData from './useOceanData';
import CollapseToggleButton from '../utils/CollapseToggleButton';
import icons from '../utils/icons';
import useCurrentTime from './useCurrentTime';

const Sunset = ({ isMotionOn }) => {

    const date = useCurrentTime();

    const latitude = localStorage.getItem('latitude');
    const longitude = localStorage.getItem('longitude');
    const sunset1uri = `https://api.sunrise-sunset.org/json?lat=${Number(latitude).toFixed(7)}&lng=${Number(longitude).toFixed(7)}`;
    //const sunset2uri = 'https://api.sunrise-sunset.org/json?lat=36.7201600&lng=-4.4203400&date=today';
    //const sunset3uri = 'https://api.sunrise-sunset.org/json?lat=36.7201600&lng=-4.4203400&date=2021-03-05';
    //const sunset4uri = 'https://api.sunrise-sunset.org/json?lat=36.7201600&lng=-4.4203400&formatted=0';
    const [sunrise, setSunrise] = useState(null);
    const [sunset, setSunset] = useState(null);
    const [darkTime, setDarkTime] = useState(null);
    // eslint-disable-next-line
    const [data, getData] = useOceanData('sunset', sunset1uri, '');
    const [surfTime, setSurfTime] = useState({ hours: 0, minutes: 0 });
    const [currentHours, setCurrentHours] = useState(useCurrentTime().hours);

    const getLocalData = (localItem) => localStorage.getItem(localItem);
    const collapseStateInit = (localItem) => getLocalData(localItem) ? getLocalData(localItem) === 'true' : true;
    const [surfTimeCollapse, setSurfTimeCollapse] = useState(collapseStateInit('surfTimeCollapse'));
    
    useEffect(() => {
        localStorage.setItem('surfTimeCollapse', surfTimeCollapse);
    }, [surfTimeCollapse]);

    useEffect(() => {
        const convertTimeToISOFormat = (time) => {
            // Split the time string into hours, minutes, and seconds
            const [timeStr, meridiem] = time.split(' ');
            const [hours, minutes, seconds] = timeStr.split(':').map(Number);

            // Convert hours to 24-hour format if necessary
            let hours24 = hours % 12;
            if (meridiem === 'PM') {
                hours24 += 12;
            }

            // Create a new Date object with the current date and the provided time
            const currentTime = new Date();
            currentTime.setHours(hours24, minutes, seconds);

            // Format the date in ISO format ('YYYY-MM-DDTHH:mm:ss')
            const isoFormat = currentTime.toISOString();

            return isoFormat;
        }

        if (data.results !== undefined) {
            //console.log(`sunset => ${JSON.stringify(data,null,2)}`)
            const sunsetTime = data.results.sunset;
            const sunsetTimeArray = sunsetTime.split(':');
            const sunsetHours = Number(sunsetTimeArray[0]) + 5;
            const sunsetMinutes = sunsetTimeArray[1];
            const sunsetDisplayTime = `${(sunsetHours > 12) ? (sunsetHours - 12) : sunsetHours}:${sunsetMinutes}`
            setSunset(sunsetDisplayTime);
            const sunriseTime = data.results.sunrise;
            const sunriseTimeArray = sunriseTime.split(':');
            const sunriseHours = Number(sunriseTimeArray[0]) + 5;
            const sunriseMinutes = sunriseTimeArray[1];
            const sunriseDisplayTime = `${sunriseHours}:${sunriseMinutes}`
            setSunrise(sunriseDisplayTime);
            const untilDark = data.results.civil_twilight_end;
            const untilDarkTimeArray = untilDark.split(':');
            const untilDarkHours = Number(untilDarkTimeArray[0]) + 5;
            const untilDarkMinutes = untilDarkTimeArray[1];
            const untilDarkDisplayTime = `${untilDarkHours}:${untilDarkMinutes}`
            //console.log(`Sunset => untilDark: ${untilDark} untilDarkDisplayTime: ${untilDarkDisplayTime}`)
            setDarkTime(untilDarkDisplayTime);
            /*
            const currentTime = new Date();
            //const currentTimeArray = String(currentTime).split(':');
            //const currentTimeHours = Number(currentTimeArray[0])+5;
            const targetTime = new Date(`${currentTime.toDateString()} ${untilDark}`);
            const timeDifference = untilDarkHours - currentHours;
            console.log(`${untilDarkHours}:${untilDarkMinutes} - ${date[0].hours}:${date[0].minutes} or ${String(date[0].currentTime)} data: ${JSON.stringify(date[0],null,2)} timeDifference: ${(Number(date[0].hours)<12) ? String('1 '+((12-Number(date[0].hours))+Number(untilDarkHours)) + ':' + (Number(60-date[0].minutes)+Number(untilDarkMinutes))) : (Number(date[0].hours) === 12) ? String(untilDarkHours+':'+((date[0].minutes<untilDarkMinutes)?String(Number(untilDarkMinutes)-Number(date[0].minutes)):String(Number(untilDarkMinutes)-Number(date[0].minutes)))) : String(' -2- ' + (Number(untilDarkHours)-Number(date[0].hours)))}`)
            //const hours = Math.floor(timeDifference / (1000 * 60 * 60));
            const hours = (Number(date[0].hours)<12) ? ((11-Number(date[0].hours))+Number(untilDarkHours)) : (Number(untilDarkHours)-(Number(date[0].hours)+1));
            //const minutes = Math.floor((timeDifference % (1000 * 60 * 60)) / (1000 * 60));
            const minutes = (Number(date[0].hours)<12) ? ((11-Number(date[0].hours))+Number(untilDarkHours)) : (Number(untilDarkHours)-(Number(date[0].hours)+1));
            */
            const currentTime = new Date().toString();
            //alert(`currentTime: ${currentTime}`)
            const currentTimeObj = new Date(currentTime);
            //alert(`currentTimeObj: ${currentTimeObj}`)
            const sunsetTimeObj = new Date(convertTimeToISOFormat(sunsetTime));
            //alert(`sunsetTimeObj: ${sunsetTimeObj}`)
            // Calculate the difference in milliseconds between current time and sunset time
            const timeDifference = sunsetTimeObj.getTime() - currentTimeObj.getTime();
            //alert(`timeDifference: ${timeDifference}`)
            // Convert milliseconds to hours and minutes
            const hoursRemaining = 17+Math.floor(timeDifference / (1000 * 60 * 60));
            //alert(`hoursRemaining: ${hoursRemaining}`)
            const minutesRemaining = 84+Math.floor((timeDifference % (1000 * 60 * 60)) / (1000 * 60));
            //alert(`minutesRemaining: ${minutesRemaining}`)
            //return [hoursRemaining, minutesRemaining];

            const displayHours = (minutesRemaining > 60) ? (hoursRemaining+1) : hoursRemaining;
            const displayMinutes = (minutesRemaining > 60) ? (minutesRemaining-60) : minutesRemaining; 

            setSurfTime({ hours:displayHours, minutes:displayMinutes });
        }
    }, [data.results]);
    const daylightHeader = () => <div>{icons.bright} DAYLIGHT {surfTime.hours}<span className='size12'>hrs</span> {surfTime.minutes}<span className='size12'>min</span></div>
    const getCurrentSunset = () => <React.Fragment>
            <div className='containerBox color-yellow size25 bg-lite size15 bold p-20'>
                <CollapseToggleButton
                    //title={`${icons.track} TIME LEFT ${surfTime.hours}hrs ${surfTime.minutes}min`}
                    title=''
                    component={daylightHeader()}
                    isCollapsed={surfTimeCollapse}
                    setCollapse={setSurfTimeCollapse}
                    align='left'
                />
            </div>
            {
                (surfTimeCollapse)
                    ? <div></div>
                    : <div className='containerBox'>
                        <div className='white p-10'>
                            {surfTime.hours} hours {surfTime.minutes} minutes
                        </div>
                        <div className='flexContainer'>
                            <div className='flex3Column containerDetail'>
                                <div className='containerDetail p-10 bg-lite color-yellow bold size20'>
                                    SUNRISE
                                </div>
                                <div className='p-10'>
                                    {sunrise}
                                    <span className='copyright bold'>
                                        AM
                                    </span>
                                </div>
                            </div>
                            <div className='flex3Column containerDetail'>
                                <div className='containerDetail p-10 bg-lite color-yellow bold size20'>
                                    SUNSET
                                </div>
                                <div className='p-10'>
                                    {sunset}
                                    <span className='copyright bold'>
                                        PM
                                    </span>
                                </div>
                            </div>
                            <div className='flex3Column containerDetail'>
                                <div className='containerDetail p-10 bg-lite color-yellow bold size20'>
                                    DARK
                                </div>
                                {/*<div className='copyright bold'>in {darkCount()}</div>*/}
                                <div className='p-10'>
                                    {darkTime}
                                    <span className='copyright bold'>
                                        PM
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
            }
    </React.Fragment>;

    return getCurrentSunset();
}

export default Sunset;