import React, { useState, useEffect } from 'react';
import Loader from '../site/Loader';
import getKey from '../utils/KeyGenerator';
import icons from '../utils/icons';
import GetDirectionIcon from '../weather/GetDirectionIcon';
import CollapseToggleButton from '../utils/CollapseToggleButton';
import getDirection from './getDirection';

const SwellDisplay = ({
    time,
    swellData,
    status,
    setStatus
}) => {

    const templateData = {
        time: 1712026800,
        interval: 3600,
        wave_height: 5.577,
        wave_direction: 281,
        wave_period: 10.00,
        wind_wave_height: 0.656,
        wind_wave_direction: 306,
        wind_wave_period: 1.95,
        wind_wave_peak_period: 2.45,
        swell_wave_height: 5.577,
        swell_wave_direction: 281,
        swell_wave_period: 10.10,
        swell_wave_peak_period: 12.30
    };
    const tempData = {
        'module': 'Waves',
        'pause': true,
        'date': '2024-04-03T14:46:47.611Z',
        'tide': 'medium',
        'stars': '1',
        'waterTemp': '66.2',
        'swell1Height': '2.0',
        'swell1Interval': '17 seconds',
        'swell1Direction': 'W',
        'swell2Height': '2.0',
        'swell2Interval': '9 seconds',
        'swell2Direction': 'NW',
        'swell1Angle': 270,
        'swell2Angle': 315,
        'windDirection': 'SE',
        'distance': '100',
        'isSwell1': false,
        'isSwell2': false,
        'isTide': false,
        'isWind': false,
        'matches': [],
        'init': false
    }
    const [swell, setSwell] = useState(swellData ?? status);

    const storedCollapse = localStorage.getItem('swellCollapse');
    const initialCollapse = storedCollapse ? storedCollapse === 'true' : true;
    const [swellCollapse, setSwellCollapse] = useState(initialCollapse);

    const getSwellHeight = () => swell.swell_wave_height ?? status.swell1Height;
    const getSwellDirection = () => swell.swell_wave_direction ?? status.swell1Direction;
    //console.log(`getSwellDirection: ${getSwellDirection()}`)
    const getSwellInterval = () => swell.swell_wave_period ?? status.swell1Interval;
    const getWaveHeight = () => swell.wave_height ?? status.swell1Height;
    const getWaveDirection = () => swell.wave_direction ?? status.swell1Direction;
    //console.log(`getWaveDirection: ${getWaveDirection()}`)
    const getWaveInterval = () => swell.wave_period ?? status.swell1Interval;
    const getWindWaveHeight = () => swell.wind_wave_height ?? status.swell2Height;
    const getWindWaveDirection = () => swell.wind_wave_direction ?? status.swell2Direction;
    //console.log(`getWindWaveDirection: ${getWindWaveDirection()}`)
    const getWindWaveInterval = () => swell.wind_wave_period ?? status.swell2Interval;

    useEffect(() => {
        setSwell(swellData ?? status);
    }, []);
    useEffect(() => {
        if (swellData) setSwell(swellData);
    }, [swellData]);

    useEffect(() => {
        setStatus(prevState => ({
            ...prevState,
            wave_height: swell.wave_height,
            wave_direction: swell.wave_direction,
            wave_period: swell.wave_period,
            wind_wave_height: swell.wind_wave_height,
            wind_wave_direction: swell.wind_wave_direction,
            wind_wave_period: swell.wind_wave_period,
            wind_wave_peak_period: swell.wind_wave_peak_period,
            swell_wave_height: Number(getSwellHeight()).toFixed(0),
            swell_wave_direction: swell.swell_wave_direction,
            swell_wave_period: swell.swell_wave_period,
            swell_wave_peak_period: swell.swell_wave_peak_period,
            swell1Height: Number(getWaveHeight()).toFixed(0),
            swell1Angle: Math.round(swell.swell_wave_direction / 5) * 5,
            swell1Direction: getDirection(swell.swell_wave_direction),
            swell1Interval: `${Number(getSwellInterval()).toFixed(0)} seconds`,
            swell2Height: Number(getWindWaveHeight()).toFixed(0),
            swell2Interval: `${Number(getWindWaveInterval()).toFixed(0)} seconds`,
            swell2Angle: Math.round(swell.wind_wave_direction / 5) * 5,
            swell2Direction: getDirection(swell.wind_wave_direction),
        }));
    }, [swell]);
    useEffect(() => {
        //console.log(`swellCollapse: ${swellCollapse}`)
        localStorage.setItem('swellCollapse', swellCollapse);
    }, [swellCollapse]);
    //console.log(`SwellDisplay => swellData: ${JSON.stringify(swellData,null,2)}`);
    //console.log(`SwellDisplay => swell: ${JSON.stringify(swell,null,2)}`);
    const isMorning = (time[0].hours > 12) ? false : true;
    const getTimeOfDay = () => (isMorning) ? 'am' : 'pm';
    const getHours = () => (isMorning) ? time[0].hours : time[0].hours - 12;
    const getDirectionArrow = (direction) => {
        if (direction >= 355 && direction < 5) {
            return icons.upArrow
        } else if (direction >= 5 && direction < 85) {
            return icons.rightUpArrow
        } else if (direction >= 85 && direction < 95) {
            return icons.rightArrow
        } else if (direction >= 95 && direction < 175) {
            return icons.rightDownArrow
        } else if (direction >= 175 && direction < 185) {
            return icons.downArrow
        } else if (direction >= 185 && direction < 265) {
            return icons.leftDownArrow
        } else if (direction >= 265 && direction < 275) {
            return icons.leftArrow
        } else if (direction >= 275 && direction < 355) {
            return icons.leftUpArrow
        }
        return icons.cancel
    }

    const swellDirectionIcon = <GetDirectionIcon
                                        direction={getSwellDirection()}
                                    />
    const swellHeader = () => <div>{icons.wave} SWELL {Number(getSwellHeight()).toFixed(0)}<span className='size12'>ft</span> {getDirection(getSwellDirection())} {getSwellDirection()} {Number(getSwellInterval()).toFixed(0)}<span className='size12'>sec</span></div>
    return <div className=''>
        <div className='containerBox bold color-yellow bg-lite p-20'>
            <CollapseToggleButton
                //title={`${icons.wave} SWELL ${Number(getSwellHeight()).toFixed(0)}ft ${getDirection(getSwellDirection())} ${getSwellDirection()} ${Number(getSwellInterval()).toFixed(0)}sec`}
                title=''
                component={swellHeader()}
                isCollapsed={swellCollapse}
                setCollapse={setSwellCollapse}
                align='left'
            />
        </div>
        {
            (swellCollapse)
                ? null
                : <div>
                    <div className='containerBox flexContainer centerVertical'>
                        <div className='flex3Column contentRight'>
                            <span className='size40 mr-30'>{icons.wave}</span>
                            {Number(getSwellHeight()).toFixed(0)}ft
                        </div>
                        <div className='flex8Column'>
                            {swellDirectionIcon}
                        </div>
                        <div className='flex5Column contentLeft'>
                            {getSwellDirection()} {getDirection(getSwellDirection())}
                        </div>
                        <div className='flexColumn contentRight'>
                            {icons.track}
                        </div>
                        <div className='flex5Column contentRight'>
                            {Number(getSwellInterval()).toFixed(0)} sec
                        </div>
                    </div>
                    <div className='containerBox flexContainer centerVertical'>
                        <div className='flex3Column contentRight'>
                            <span className='size30 mr-35'>{icons.wave}</span>
                            {Number(getWaveHeight()).toFixed(0)}ft
                        </div>
                        <div className='flex8Column'>
                            <GetDirectionIcon
                                direction={getWaveDirection()}
                            />
                        </div>
                        <div className='flex5Column contentLeft'>
                            {getWaveDirection()} {getDirection(getWaveDirection())}
                        </div>
                        <div className='flexColumn contentRight'>
                            {icons.track}
                        </div>
                        <div className='flex5Column contentRight'>
                            {Number(getWaveInterval()).toFixed(0)} sec
                        </div>
                    </div>
                    <div className='containerBox flexContainer centerVertical'>
                        <div className='flex3Column contentRight'>
                            <span className='mr-30'>{icons.wind} {icons.wave}</span>
                            {Number(getWindWaveHeight()).toFixed(0)}ft
                        </div>
                        <div className='flex8Column'>
                            <GetDirectionIcon
                                direction={getWindWaveDirection()}
                            />
                        </div>
                        <div className='flex5Column contentLeft'>
                            {getWindWaveDirection()} {getDirection(getWindWaveDirection())}
                        </div>
                        <div className='flexColumn contentRight'>
                            {icons.track}
                        </div>
                        <div className='flex5Column contentRight'>
                            {Number(getWindWaveInterval()).toFixed(0)} sec
                        </div>
                    </div>
                </div>
        }

    </div>

}

export default SwellDisplay;